@keyframes #{$animation}
    0%
        background-image: url($background)
    25%
        background-image: url($background-two)
    50%
        background-image: url($background-three)
    75%
        background-image: url($background-four)
    100%
        background-image: url($background)

.header
    height: 95vh
    background-repeat: no-repeat
    background-size: cover
    background-position: center
    animation: #{$animation} $duration infinite
    color: white

    &-gradient
        height: 100%
        width: 100%
        @include display
        background: linear-gradient(rgba(0, 53, 84, .4), rgba(0, 53, 84, .4))

        .itens
            margin-left: 10rem
            width: 42rem
            justify-content: start

            @include tablet
                margin: 10rem

            @include mobile
                margin: 7rem

            @include mobile-low
                margin: 3rem

            h1
                font-size: 80px
                @include tablet
                    font-size: 60px

                @include mobile
                    font-size: 52px

            p
                font-size: 25px
                @include tablet
                    font-size: 20px

                @include mobile
                    font-size: 17px
