.navbar
    @include display
    padding: .7rem 10rem .7rem 10rem
    background-color: #051923
    color: white

    @include mobile
        flex-direction: column
        gap: 1rem

    @include mobile-phone
        padding: .7rem 1rem .7rem 1rem

    &-itens
        @include display
        gap: 2rem

        @include mobile-phone
            gap: 1rem

        @include mobile-low
            display: none

        a
            text-decoration: none
            color: white
            font-size: 1.1rem

            &:hover
                color: #d4d4d4
