.card-reviews
    height: 23rem
    width: 28rem
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column
    gap: 1rem
    padding: 2rem
    box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.3)

    @include desktop-low
        height: 22rem
        width: 25rem

    @include low-resolution
        height: 20rem
        width: 23rem

        p
            font-size: 15px

        h2
            font-size: 18px

    @include mobile-phone
        height: 19.9rem
        width: 16.9rem

    .stars
        color: rgb(233, 221, 50)
        font-size: 1.3rem

        @include low-resolution
            font-size: 1.1rem

    img
        height: 7rem
        width: 7rem
        object-fit: cover
        border-radius: 50%

        @include desktop-low
            height: 5rem
            width: 5rem

        @include low-resolution
            height: 4.5rem
            width: 4.5rem
