.card
    @include display
    flex-direction: column
    width: 14rem
    height: 11rem
    text-align: center
    padding: .5rem
    box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.3)

    @include low-resolution
        width: 13rem
        height: 10rem

    h1
        font-size: 55px
        margin-bottom: -1rem
        color: $color-itens

        @include low-resolution
            font-size: 50px

        @include low-resolution-gap
            font-size: 45px

    h2
        @include low-resolution
            font-size: 20px

        @include low-resolution-gap
            font-size: 17px
