.card-destinations
    height: 30rem
    width: 25rem
    background-repeat: no-repeat
    background-size: cover
    background-position: center
    padding: 25rem 2rem 6rem 2rem

    @include low-resolution
        height: 27rem
        width: 22rem

    @include mobile
        height: 23rem
        width: 18rem

    &:hover
        outline: solid 5px $hover
        transition: .1s
