.footer
    color: white
    background-color: black
    height: 45vh

    @include mobile-phone
        height: 60vh

    .itens
        padding: 5rem
        display: flex
        justify-content: center
        flex-direction: row
        gap: 10rem

        @include tablet
            gap: 5rem

        @include mobile
            gap: 3.5rem

        @include mobile-phone
            gap: 1.5rem
            flex-direction: column

        .more, .contact, .social
            h3
                margin-bottom: 1.5rem

            @include mobile
                font-size: .9rem

            p
                padding-bottom: 0.5rem
                cursor: pointer

            .icon-container
                display: flex
                gap: .5rem
                .icon
                    font-size: 2.5rem
                    cursor: pointer

                @include tablet
                    flex-direction: column

                @include mobile
                    font-size: 1.4rem

                @include mobile-phone
                    flex-direction: row
