.destinations-container
    height: 80vh
    background-color: $backgroud-color
    color: white
    text-align: center

    @include tablet
        height: 180vh

    h1
        padding-top: 4rem

    .popular-destinations
        display: flex
        justify-content: space-between
        margin: 5rem 10rem 0rem 10rem

        @include low-resolution-gap
            margin: 5rem 3rem 0rem 3rem

        @include tablet
            flex-direction: column
            align-items: center
            justify-content: center
            gap: 3rem
