.about-Container
    height: 80vh
    margin-bottom: 8rem

    @include mobile-phone
        height: 130vh

    @include mobile-low
        height: 120vh

    .about
        height: 30rem
        display: flex
        justify-content: space-between
        align-items: center
        gap: 10rem
        margin-bottom: 10rem

        @include low-resolution
            gap: 5rem

        @include low-resolution-gap
            gap: 4rem

        @include tablet
            flex-direction: column
            margin: 10rem
            justify-content: center
            align-content: center
            margin-bottom: 6rem

        @include tablet
            margin: 5rem
            margin-bottom: 6rem

        &-imgs
            padding: 0rem 10rem 0rem 10rem

            @include tablet
                padding: 0rem 10rem 0rem 0rem

            @include mobile
                padding: 3rem 10rem 0rem 0rem

            @include mobile-phone
                padding: 11rem 10rem 0rem 0rem

            @include mobile-low
                display: none

            .back
                width: 30rem
                @include low-resolution
                    width: 23rem

                @include mobile
                    width: 18rem

                @include mobile-phone
                    width: 14rem

            .front
                width: 30rem
                margin: 10rem 7rem 7rem 12rem
                position: absolute

                @include low-resolution
                    width: 23rem
                    margin: 8rem 5rem 5rem 10rem

                @include mobile
                    width: 18rem

                @include mobile-phone
                    width: 14rem
                    margin: 4rem 3rem 5rem 7rem

            img
                border-radius: 5px

        &-text
            display: flex
            flex-direction: column
            gap: 1rem
            padding: 5rem 10rem 0rem 10rem

            h1
                color: black
                @include tablet
                    text-align: center

            @include low-resolution-gap
                padding: 5rem 10rem 0rem 2rem

            @include tablet
                padding: 5rem 2rem 0rem 2rem

            @include mobile
                padding: 5rem 0rem 0rem 0rem

            @include mobile-phone
                padding: 3rem 0rem 0rem 0rem
                width: 20rem

            @include mobile-low
                padding: 1rem 0rem 0rem 0rem
                width: 15rem

    .cards
        @include display
        flex-direction: row
        margin: 0rem 20rem 0rem 20rem

        @include low-resolution-gap
            gap: 3rem
            margin: 0rem 13rem 0rem 13rem

        @include tablet
            display: grid
            justify-content: center
            align-itens: center
            grid-template-columns: repeat(2, 1fr)
            grid-template-rows: repeat(2, 1fr)
            grid-gap: 30px
            margin: 0rem 20rem 0rem 20rem

        @include mobile
            margin: 8rem 20rem 0rem 20rem

        @include mobile-phone
            display: flex
            flex-direction: column
            justify-content: center
            align-content: center
            margin: 14rem 3rem 0rem 3rem

        @include mobile-low
            display: flex
            flex-direction: column
            justify-content: center
            align-content: center
            margin: 7rem 3rem 0rem 3rem
